import Button from '@mui/material/Button'
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import CircularProgress from '@mui/material/CircularProgress';
import { DataGrid } from '@mui/x-data-grid';
import React from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';

import { ApolloClient, InMemoryCache, gql } from '@apollo/client'
import { CssBaseline, Link } from '@mui/material';

const theme = createTheme({
  status: {
    danger: '#FFBE40',
  },
  palette: {
    primary: {
      main: '#1A77CD',
      darker: '#262626',
      contrastText: '#F0F0F0',
    },
    success: {
      main: '#0EAB4D',
      contrastText: '#F0F0F0',
    },
    background: {
      default: '#262626',
    }
  },
  typography: {
    h4: {
      color: '#F0F0F0',
    },
    h5: {
      color: '#F0F0F0',
    }
  },
});

function App() {
  const [veJoeData, setVeJoeData] = React.useState([]);
  const [latestBlock, setLatestBlock] = React.useState("");
  const [loading, setLoading] = React.useState(false);

  React.useEffect(() => {
    handleRefreshData();
  }, [])

  const formatNumber = (number) => {
    // this puts commas into the number eg 1000 goes to 1,000,
    // i pulled this from stack overflow, i have no idea how it works
    return Math.floor(number)
      .toString()
      .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
  };

  const currencyFormatter = (params) => {
    return '$' + formatNumber(params.value);
  };

  const columns = [
    { field: 'id', headerClassName: 'yy-theme--header', headerName: 'User Address', width: 250,
      renderCell: (params) => (
        <Link href={`https://snowtrace.io/address/${params.value}`} target="_blank" color="primary.contrastText">{params.value}</Link>
      )},
    { field: 'joeStaked', headerClassName: 'yy-theme--header', headerName: 'JOE Staked', type: 'number', width: 120, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'joeStakedUSD', headerClassName: 'yy-theme--header', headerName: 'JOE Staked USD', type: 'number', width: 120, valueFormatter: currencyFormatter },
    { field: 'totalVeJoeMinted', headerClassName: 'yy-theme--header', headerName: 'Total veJOE Minted', type: 'number', width: 120, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'totalVeJoeBurned', headerClassName: 'yy-theme--header', headerName: 'Total veJOE Burned', type: 'number', width: 120, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'veJoeBalance', headerClassName: 'yy-theme--header', headerName: 'veJOE Balance', type: 'number', width: 120, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'joeStakedRanking', headerClassName: 'yy-theme--header', headerName: 'Ranking (Staked JOE)', type: 'number', width: 100, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'veJoeBalanceRanking', headerClassName: 'yy-theme--header', headerName: 'Ranking (veJOE Balance)', type: 'number', width: 100, valueFormatter: (params) => { return formatNumber(params.value) } },
  ];

  const VEJOEAPIURL = 'https://api.thegraph.com/subgraphs/name/traderjoe-xyz/vejoe'
  const LATESTBLOCKAPIURL = 'https://api.thegraph.com/index-node/graphql'

  const latestBlockQuery = `
    query {
      indexingStatusForCurrentVersion(subgraphName: "traderjoe-xyz/vejoe") {
          chains(network: "avalanche") {
              network
              latestBlock {
                  hash
                  number
              }
          }
      }
    }
  `

  const query = `
    query users($id: String, $latestBlock: Int) {
      users(first: 1000, orderBy: id, orderDirection: asc, where: {id_gt: $id}, block: {number: $latestBlock}) {
        id
        joeStaked
        joeStakedUSD
        totalVeJoeMinted
        totalVeJoeBurned
        veJoeBalance
      }    
    }
  `

  const handleLatestBlock = async (newLatestBlock) => {

    setLatestBlock(newLatestBlock);

    const veJoeClient = new ApolloClient({
      uri: VEJOEAPIURL,
      cache: new InMemoryCache(),
    });

    let last_id = "";
    let all_users = [];
    let new_users = [0];

    while (new_users.length > 0) {
      new_users = await veJoeClient
        .query({
          query: gql(query),
          variables: {
            id: last_id,
            latestBlock: Number(newLatestBlock),
          },
        })
      new_users = new_users["data"]["users"]
      all_users = all_users.concat(new_users)
      if (new_users.length > 0) {
        last_id = new_users[new_users.length - 1]["id"]
      }
    }
    all_users = all_users.sort((a, b) => Number(b.joeStaked) - Number(a.joeStaked))
                         .map((user, i) => ({joeStakedRanking: i + 1, ...user}))
    all_users = all_users.sort((a, b) => Number(b.veJoeBalance) - Number(a.veJoeBalance))
                         .map((user, i) => ({veJoeBalanceRanking: i + 1, ...user}))

    setVeJoeData(all_users);
    setLoading(false);

  };

  const handleRefreshData = () => {

    setLoading(true);

    const latestBlockClient = new ApolloClient({
      uri: LATESTBLOCKAPIURL,
      cache: new InMemoryCache(),
    });

    latestBlockClient
      .query({
        query: gql(latestBlockQuery),
      })
      .then((data) => handleLatestBlock(data["data"]["indexingStatusForCurrentVersion"]["chains"][0]["latestBlock"]["number"]))
      .catch((err) => {
        console.log('Error fetching data: ', err)
      });
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container sx={{
        height: '100vh',
        padding: "10px",
        '& .super-app-theme--yy': {
          bgcolor: "success.main",
        }
      }}>
        <Grid container spacing={2} align="center" sx={{ height: '100%', }}>
          <Grid container item alignItems="center">
            <Grid item xs={6} sm={4}>
              <Typography variant="h4">Top veJOE stakers</Typography>
            </Grid>
            <Grid item xs={6} sm={4}>
              <Typography variant="h5">Data as of block: {latestBlock}</Typography>
            </Grid>
            <Grid item xs={6} sm={4}>
              <Box sx={{ m: 1, position: 'relative' }}>
                <Button
                  variant="contained"
                  onClick={handleRefreshData}
                  disabled={loading}
                >
                  Refresh data
                </Button>
                {loading && (
                  <CircularProgress
                    size={24}
                    sx={{
                      color: "white",
                      position: 'absolute',
                      top: '50%',
                      left: '50%',
                      marginTop: '-12px',
                      marginLeft: '-12px',
                    }}
                  />
                )}
              </Box>
            </Grid>
          </Grid>
          <Grid item xs={12} height={"90vh"}>
            <DataGrid
              rows={veJoeData}
              columns={columns}
              density='compact'
              headerHeight={80}
              // pageSize={20}
              initialState={{
                sorting: {
                  sortModel: [{ field: 'joeStaked', sort: 'desc' }],
                },
              }}
              rowsPerPageOptions={[10, 25, 50, 100]}
              getRowClassName={(params) => `super-app-theme--${params.row.id === "0xe7462905b79370389e8180e300f58f63d35b725f" ? "yy" : "other"}`}
              sx={{
                boxShadow: 2,
                border: 2,
                borderColor: 'success.main',
                color: 'primary.contrastText',
                '& .MuiDataGrid-cell:hover': {
                  color: 'primary.main',
                },
                '& .MuiDataGrid-columnHeaders': {
                  backgroundColor: 'success.main',
                },
                '& .MuiDataGrid-columnHeaderTitle': {
                  textOverflow: "clip",
                  whiteSpace: "break-spaces",
                  lineHeight: 1
                },
                '& .MuiTablePagination-root': {
                  color: 'primary.contrastText',
                },
                '& .MuiSelect-icon': {
                  color: 'primary.contrastText',
                },
                '& .MuiButtonBase-root': {
                  color: 'primary.contrastText',
                },
                '& .MuiMenuItem-root': {
                  color: 'primary.darker',
                },
              }}
            />
          </Grid>
        </Grid>
      </Container>
    </ThemeProvider>
  );
}

export default App;
